<template>
  <div id="my">
	   <div class="head fx fx-ver fx-ac fx-jc">
      <img class="bd-radius" src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1532091386269&di=197a274d65a94e2d70a864834442e9ac&imgtype=0&src=http%3A%2F%2Fimg.zcool.cn%2Fcommunity%2F01460b57e4a6fa0000012e7ed75e83.png" width="100" height="100">
      <span>18827342863</span>
     </div>
     <ul v-for="(item, index) in list" :key="index">
       <li  v-for="(item1, index) in item" :key="index" @click="go(item1)">
        <a class="cell" :href="item1.href">
          <div class="wrap">
            <span class="iconfont" :class="item1.icon"></span>
            <span>{{item1.title}}</span>
          </div>
          <span class="iconfont icon-youbianjiantou"></span>
          </a>
       </li>
     </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      list: {
        '1': [
          {icon: 'icon-guanggaozhuhuodongtuiguang', title: '我的广告', path: '/home'}
        ],
        '2': [
          {icon: 'icon-guanyuwomen', title: '关于我们', path: '', href: ''},
          {icon: 'icon-bangzhu', title: '帮助', href: ''}
        ],
        '3': [
          {icon: 'icon-shezhi', title: '设置', path: '/my/set'}
        ]
      }
    }
  },
  methods: {
    go (page) {
      this.$router.push(page.path)
    }
  },
  mounted () {
  }
}
</script>

<style scoped lang="scss">
.head{
  height: 150px;
  background: #26a2ff;
  border-bottom: 1px solid #eee;
}
ul{
  margin: 10px 0;
  a:active{
    background: #ccc;
  }
}
.cell{
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 48px;
  padding:0 20px;
  color: #333;
  background: #fff;
  .wrap{
    font-size: 16px;
    .iconfont{
      margin-right: 10px;
      font-size: 18px;
    }
  }
}
</style>



